import {Title} from "../../components/el-i"
import ElRadio from "element-ui/packages/radio/src/radio.vue";
import IQesTitle from "@/components/QuestionTitle.vue";
export default {
    data () {
        return {
            way: 'one-add',
            questionType: "choose-one",
        }
    },
    components:{
        IQesTitle,
        ElRadio,
        [Breadcrumb.name]: Breadcrumb,
        [BreadcrumbItem.name]: BreadcrumbItem,
        [Button.name]: Button,
        [RadioGroup.name]: RadioGroup,
        [RadioButton.name]: RadioButton,
        [Title.name]: Title,
    },
    created (){
        if (["choose-one","choose-more", "completion", "jquiz", "judge"].indexOf(this.questionType) == -1){
            this.questionType =  'choose-one';
        }
    },
    methods:{
        unSelect: function () {
            console.log('way', this.way);
            return;
        },
        commit: function () {
            console.log("889998")
        },
        toWay: function(value){
            this.$router.push({name:value})
        },
        toType: function (value) {
            if (value.startsWith("choose")){
                this.$router.push({name:'qes-choose',query:{type:value}});
            }
        }
    }
}
